import React, { Component } from 'react';
import updateHandle from './index'
import {Added} from './AddedData'
import detectPhone from './detectPhone';

class Arabic extends Component {

    state = {phone: detectPhone(), grams: 100}

    gramHandle = (event) => {
        if (event.target.value < 1) { this.setState({grams: event.target.min})
        } else if (event.target.value > 10000) {this.setState({grams: event.target.max})
        } else {this.setState({grams: event.target.value})}
    }

    calc = x => Number((((x * this.state.grams) / 100).toFixed(1)).toString());
    calcKcal = x => Number(((x * this.state.grams) / 100).toFixed(0));
    percentProtein = () => Math.floor((Number(this.props.protein) * 400)/Number(this.props.cal));
    percentFat = () => Math.floor((Number(this.props.fat) * 900)/Number(this.props.cal));
    percentCarb = () => (100 - (this.percentProtein() + this.percentFat()));

    PhoneViewArabic() {
        return(
            <div className='cardPhone' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                let merged = {...this.props, ...{gram: this.state.grams}};
                                Added.push(merged);
                                updateHandle();
                                document.getElementById('sumPhone').style.display='inline-flex';
                                window.scrollTo(0,0)
                            }}>add
                </button>

                <div className="kcalDetailsPhoneAr">
                    <div className='percentNumsPhoneArabic'>
                        <b className='b1'>{this.percentCarb()}%</b>
                        <b className='b2'>{this.percentProtein()}%</b>
                        <b className='b3'>{this.percentFat()}%</b><br />
                     </div>
                    <div id='bars'>
                        <div id='barContainer'>
                            <div id='carbBar' style={{width: (this.percentCarb()*0.75)}}></div>
                            <b>سكريات</b>
                        </div>
                        <div id='barContainer'>
                            <div id='proteinBar' style={{width: (this.percentProtein()*0.75)}}></div>
                            <b>بروتين</b>
                        </div>
                        <div id='barContainer'>
                            <div id='fatBar' style={{width: (this.percentFat()*0.75)}}></div>
                            <b>دهون</b>
                        </div>
                    </div>
                    <h3>
                    <p>{this.calc(this.props.fat)} غ</p> دهون:
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>

                <div className='titlePhoneAr'>
                    <h2>{this.props.name}</h2><br></br>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5><br></br>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> غرام</h6>
                </div>

            </div>
        )
    }

    PcViewArabic() {
        return(
            <div className='cardPc' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                let merged = {...this.props, ...{gram: this.state.grams}};
                                Added.push(merged);
                                updateHandle();
                                if (Added.length !== 0) {document.getElementById('cardsContainer').style.marginLeft='200px'}
                                document.getElementById('sumPc').style.display = 'inline-flex'
                                }}>add
                </button>
                
                <div className="kcalDetailsPcAr">
                    <div className='percentNumsPc'>
                        <b className='b1'>{this.percentCarb()}%</b>
                        <b className='b2'>{this.percentProtein()}%</b>
                        <b className='b3'>{this.percentFat()}%</b><br />
                     </div>
                    <div id='bars'>
                        <div id='barContainer'>
                            <div id='carbBar' style={{width: (this.percentCarb()*0.75)}}></div>
                            <b>سكريات</b>
                        </div>
                        <div id='barContainer'>
                            <div id='proteinBar' style={{width: (this.percentProtein()*0.75)}}></div>
                            <b>بروتين</b>
                        </div>
                        <div id='barContainer'>
                            <div id='fatBar' style={{width: (this.percentFat()*0.75)}}></div>
                            <b>دهون</b>
                        </div>
                    </div>
                    <h3>
                    دهون:<p>{this.calc(this.props.fat)} غ</p> 
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>

                <div className='titlePcAr'>
                    <h4>{this.props.name}</h4>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> غرام</h6>
                </div>

            </div>
        )
    }

    render() {        
        if(this.state.phone) {return this.PhoneViewArabic()} else {return this.PcViewArabic()}
    }
}

export default Arabic